<template>
  <div>
      <h1>Localisation</h1>
        <div class="card">
            <div>
                <label>Pays</label>
                <input type="text" v-model="localisation.pays">                
            </div>
            <div>
                <label>Région</label>
                <input type="text" v-model="localisation.region">
            </div>
            <div>
                <label>Département</label>
                <input type="text" v-model="localisation.departement">
            </div>
            <div>
                <label>Arrondissement</label>
                <input type="text" v-model="localisation.arrondissement">            
            </div>
            <b-button pill variant="success" @click="addLocalisation(localisation)">Save</b-button>
            <b-button pill variant="info" @click="cancelLocalisation()">Cancel</b-button>
     </div>
  </div>
</template>

<script>
import moment from 'moment'

export default {
    name:'LocalisationEdit',
    data(){
        return {
            localisation: this.$store.getters.getDetailLocalisation
        }
    },

    methods: {
        addLocalisation(localisation){
             this.localisation.updatedAt= moment.now();
          
            //this.pays=moment(moment.now()).format('Do MMMM YYYY, h:mm:ss a');
            this.$store.commit('addLocalisation',localisation);
            this.$router.push("/localisation-list");
        },
        cancelLocalisation(){
            this.$store.commit('setDetailLocalisation',"null");
            this.$router.push("/localisation-list");
        }
    }
}
</script>

